










import {
  defineComponent, computed, getCurrentInstance, watch,
} from '@vue/composition-api';
import axios from '@/utils/axios';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import { getContextUser } from '@/loaders/context';
import { Meeting } from './typings';


const getMeetingId = async () => {
  // 防止页面props没有meetingId:比如会议库刷新后拿不到MeetingId
  // meetingId统一在这里获取,后续meetingId使用meetingInfo.id
  return (await axios({
    url: '/meeting/meeting/id',
  })).data;
};


const Index = defineComponent({
  name: 'up-meeting',
  components: {},
  props: {
    meetingId: {
      type: String,
      required: true,
    },
  },

  setup(props) {
    const _this = getCurrentInstance() as InstanceType<typeof Index>;

    const user = computed(() => getContextUser());
    const checkPermission = async (meeting:Meeting) => {
      const checkPaths = ['/up-meeting/home'];
      if (!checkPaths.includes(_this?.$route.path)) return;
      if (!Object.keys(meeting).length) {
        // 会议已被取消
        _this.$router.push({
          path: '/403',
          query: {
            img: require('@/assets/img/error/network.png'),
            message: '会议已被取消',
          },
        });
      } else {
        const userId = user.value?.id || '';
        const { data: usersUsable } = await axios.post<Record<string, boolean>>('/meeting/member/exists', {
          userIds: [userId],
          cooperationid: meeting.cooperationId,
        });
        if (!usersUsable[userId]) {
          // 没有会议权限
          _this.$router.push({ path: '/403' });
        }
      }
    };
    const meeting = useLoader(async ({ meetingId }) => {
      meetingId ??= await getMeetingId();
      // 可能有页面不需要meetingId 比如会议库,没传meetingId也不阻塞线程
      const _meeting = (await axios.get<Meeting>('meeting/meeting/info', { params: { meetingId } })).data || {};
      checkPermission(_meeting);
      return _meeting;
    }, props);


    return {
      meeting,
      user,
    };
  },
});

export default Index;
